import React from "react";
import { Icon } from "../../../../constants";
import { useGreenhouseProbability } from "../../../../hooks/useGreenhouseProbability";
import { Plant } from "../../../../types";

import "./Greenhouse.scss";

interface Props {
  plant: Plant;
}

const Greenhouse = ({ plant }: Props) => {
  const { eventProbabilities, eventToday, actualWeather, totalProbabilities } =
    useGreenhouseProbability(plant);
  console.log(eventProbabilities);
  console.log(eventToday);
  console.log(totalProbabilities);

  return (
    <div className="flex-column">
      <div className="flex-column gap-5px">
        <div className="greenhouse-grid">
          <p>Clima</p>
          <p>LE</p>
          <p>Prob.</p>
        </div>
        {Object.keys(eventProbabilities).map((event) => {
          const eventProbability =
            eventProbabilities[event as keyof typeof eventProbabilities];
          return (
            <div className="greenhouse-grid">
              <p className="subtext text-left">{event.substring(0, 12)}</p>
              <p
                className="subtext"
                style={{
                  color:
                    eventProbability.le > 0
                      ? "green"
                      : eventProbability.le < 0
                      ? "red"
                      : "white",
                }}
              >
                {eventProbability.le.toFixed(2)}
              </p>
              <p className="subtext">
                {eventProbability.probability.toFixed(2)}
              </p>
            </div>
          );
        })}
      </div>
      <div className="greenhouse-grid">
        <div>{totalProbabilities.favor.toFixed(2)}</div>
        <div>{totalProbabilities.against.toFixed(2)}</div>
        <div>{totalProbabilities.neutral.toFixed(2)}</div>
      </div>
    </div>
  );
};

export default Greenhouse;
